(function ($) {
  "use strict";
  $(document).ready(() => {
    $('.mdb-select').materialSelect({});
    $(".button-collapse").sideNav();
    new WOW().init();
  });
  $('.count').each(function () {
    var size = $(this).text().split(".")[1] ? $(this).text().split(".")[1].length : 0;
    $(this).prop('Counter', 0).animate({
      Counter: $(this).text()
    }, {
      duration: 2000,
      step: function (func) {
        $(this).text(parseFloat(func).toFixed(size));
      }
    });
  });
  new WOW().init();
  objectFitImages();
  jarallax(document.querySelectorAll(".jarallax"));
  jarallax(document.querySelectorAll(".jarallax-keep-img"), {
    keepImg: true
  });
  var owl = $("#galery-slide");
  var fSlide = $("#footer-slide");
  var fcopy = document.getElementsByClassName("footer-copyright")[0];
  var copy = "Copyright &copy; ";
  var ContentLeft = document.getElementById("main-content-left");
  var mdbLight = document.getElementById("popupGalery");
  var scrWidth = $(window).width();
  if (scrWidth > 992 && ContentLeft !== null) {
    document.getElementById("main-content-right").style.height =
      ContentLeft.offsetHeight + "px";
  }
  if (mdbLight !== null) {
    mdbLight.innerHTML += window.atob('');
  }
  fcopy.insertAdjacentHTML(
    "afterbegin",
    copy.concat(new Date().getFullYear()).concat(" All Rights Reserved by ")
  );
  fcopy.insertAdjacentHTML(
    "beforeend",
    "<br><span>Made with <i class='fa fa-heart pulse'></i> by <a href='https://bit.ly/3cbVfOh' target='_blank'>Pustaka Digital</a></span>"
  );
  // Collapsible Card
  $('a[data-action="collapse"]').on("click", function (e) {
    e.preventDefault();
    $(this)
      .closest(".card")
      .children(".card-content")
      .collapse("toggle");
    $(this)
      .closest(".card")
      .find('[data-action="collapse"]')
      .toggleClass("rotate");
  });
  
  $('[data-toggle="tooltip"]').tooltip()
  // Toggle fullscreen
  $('a[data-action="expand"]').on("click", function (e) {
    e.preventDefault();
    $(this)
      .closest(".card")
      .find('[data-action="expand"] i')
      .toggleClass("fa-expand fa-compress");
    $(this)
      .closest(".card")
      .toggleClass("card-fullscreen");
  });
  // Close Card
  $('a[data-action="close"]').on("click", function () {
    $(this).closest(".card").removeClass().slideUp("fast");
  });
  $(".heading-elements-toggle").on("click", function () {
    $(this)
      .next(".heading-elements")
      .toggleClass("visible");
  });

  function toggleDropdown(e) {
    var _d = $(e.target).closest(".dropdown"),
      _m = $(".dropdown-menu", _d);
    setTimeout(
      function () {
        var shouldOpen = e.type !== "click" && _d.is(":hover");
        _m.toggleClass("show", shouldOpen);
        _d.toggleClass("show", shouldOpen);
        $('[data-toggle="dropdown"]', _d).attr("aria-expanded", shouldOpen);
      },
      e.type === "mouseleave" ? 300 : 0
    );
  }
  $(document).ready(function () {
    $("#searchKecDes").on("keyup", function () {
      var value = $(this).val().toLowerCase();
      $("#dtKecDes *").filter(function () {
        $(this).toggle($(this).text().toLowerCase().indexOf(value) > -1);
      });
    });
    $("#layananSearch").on("keyup", function () {
      var value = $(this).val().toLowerCase();
      $("#layananDIV *").filter(function () {
        $(this).toggle($(this).text().toLowerCase().indexOf(value) > -1);
      });
    });
  });
  $("body")
    .on("mouseenter mouseleave", ".dropdown", toggleDropdown)
    .on("click", ".dropdown-menu a", toggleDropdown);

  $(".parallax").each(function () {
    if ($(this).attr("data-bg")) {
      $(this).css({
        "background-image": "url(" + $(this).data("bg") + ")"
      });
    }
  });
  owl.owlCarousel({
    autoplay: true,
    lazyLoad: true,
    loop: true,
    margin: 20,
    animateOut: "fadeOut",
    animateIn: "fadeIn",
    responsiveClass: true,
    autoHeight: true,
    autoplayTimeout: 7000,
    smartSpeed: 800,
    responsive: {
      0: {
        items: 2
      },

      600: {
        items: 2
      },

      1024: {
        items: 2
      },

      1366: {
        items: 3
      }
    }
  });
  fSlide.owlCarousel({
    autoplay: true,
    lazyLoad: true,
    loop: true,
    margin: 20,
    animateOut: "fadeOut",
    animateIn: "fadeIn",
    responsiveClass: true,
    autoHeight: true,
    autoplayTimeout: 7000,
    smartSpeed: 800,
    dots: false,
    responsive: {
      0: {
        items: 2
      },

      600: {
        items: 3
      },

      1024: {
        items: 5
      },

      1366: {
        items: 5
      }
    }
  });

  $(".next-portofolio").click(function () {
    owl.trigger("next.owl.carousel");
  });
  $(".prev-portofolio").click(function () {
    owl.trigger("prev.owl.carousel");
  });
  var script = document.createElement('script');
  script.onload = function () {
    //do stuff with the script
  };
  script.src = "https://widget.kominfo.go.id/gpr-widget-kominfo.min.js";

  document.body.appendChild(script);

  if ($('#screensaver').length > 0) {
    $(document).ready(function () {
      var mousetimeout;
      var screensaver_active = false;
      var idletime = $('#screensaver').attr("data-time");

      function show_screensaver() {
        $('#screensaver').fadeIn();
        screensaver_active = true;
        screensaver_animation();
      }

      function stop_screensaver() {
        $('#screensaver').fadeOut();
        screensaver_active = false;
      }

      function getRandomColor() {
        var letters = '0123456789ABCDEF'.split('');
        var color = '#';
        for (var i = 0; i < 6; i++) {
          color += letters[Math.round(Math.random() * 15)];
        }
        return color;
      }
      function timeout() {
        mousetimeout = setTimeout(function () {
          show_screensaver();
        }, 1000 * idletime);
      }

      timeout();
      $(document).click(function () {
        clearTimeout(mousetimeout);
        if (screensaver_active) {
          stop_screensaver();
        }
        timeout();
      });

      $(document).mousemove(function () {
        clearTimeout(mousetimeout);
        timeout();
      });

      function screensaver_animation() {
        if (screensaver_active) {
          $('#screensaver').animate({
              backgroundColor: getRandomColor()
            },
            400,
            screensaver_animation);
        }
      }
    });
  }



})(jQuery);
